.button {
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    touch-action: manipulation;
    position: relative;
    font-family: $bold;
    font-size: 16px;
    line-height: 22px;
    vertical-align: middle;
    text-align: center;
    white-space: normal;
    word-wrap: normal;
    border: none;
    background-color: $accent;
    color: #ffffff;
    cursor: pointer;
    padding-left: 30px;
    padding-right: 30px;
    min-height: 54px;
    cursor: pointer;
    transition: background-color .3s;
    white-space: nowrap;
    text-decoration: none;

    &:hover {
        background-color: #33A992; }

    &__icon {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 10px;

        svg {
            width: 100%;
            height: 100%;
            fill: #ffffff; } }


    &--full-width {
        width: 100%; }

    &--secondary {
        background-color: #EAFBF7;
        color: $accent;

        &:hover {
            background-color: #D4F7EF;
            color: $accent; } }

    &--medium {
        min-height: 48px; }

    &--mobile-small {
        @media screen and (max-width: 768px) {
            min-height: 48px; } } }

