.item {

    &__header {
        display: flex;
        align-items: center; }

    &__header-title {
        font-size: 20px;
        line-height: 32px;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 0; }

    &__header-icon {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $accent;
        margin-right: 12px;

        svg {
            width: 14px;
            height: 14px;
            fill: #ffffff; } }

    &__header-description {
        font-size: 16px;
        margin-top: 16px;
        margin-bottom: 0;
        line-height: 28px; } }
