.side-nav {
    margin: 0;
    padding: 0;
    list-style: none;

    &__item {

        &:not(:last-child) {
            margin-bottom: 4px; } }

    &__link {
        font-size: 16px;
        line-height: 24px;
        color: $main-dark;
        font-family: $bold;
        text-decoration: none;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 16px;
        padding-right: 16px;
        display: block;
        border-radius: 2px;

        &:hover {
            color: $accent;
            background-color: #EAFBF7; } } }
