.input-search {
    height: 40px;
    display: flex;
    position: relative;


    // Иконка поиска

    &__icon {
        position: absolute;
        content: '';
        left: 12px;
        width: 18px;
        height: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 50%;
        transform: translateY(-50%);
        pointer-events: none;

        svg {
            width: 100%;
            height: 100%;
            fill: #A4A2A2; } }


    //  Поле ввода

    &__field {
        background-color: #F5F5F5;
        color: #797979;
        font-size: 14px;
        padding-left: 42px;
        padding-top: 0;
        padding-bottom: 0;
        padding-right: 0;
        font-family: $bold;
        border: none;
        border-top-left-radius: 2px;
        border-bottom-left-radius: 2px;
        height: 100%;
        width: 230px;
        box-sizing: border-box;
        width: 100%;


        &::placeholder {
            color: #A4A2A2; } }


    // Кнопка

    &__button {
        border: none;
        height: 100%;
        border-top-right-radius: 2px;
        border-bottom-right-radius: 2px;
        color: #ffffff;
        font-family: $bold;
        background-color: $accent;
        cursor: pointer;
        transition: background-color .2s;
        padding-left: 20px;
        padding-right: 20px;

        &:hover {
            background-color: #33A992; } }

    &--medium {
        height: 48px; } }

