.breadcrumbs {
    margin: 0;
    list-style: none;
    padding: 0;
    display: flex;
    font-family: $bold;
    color: $main-dark;
    font-size: 14px;
    line-height: 20px;
    padding-top: 28px;
    padding-bottom: 28px;

    &__item {
        padding: 0;
        white-space: nowrap;

        &:not(:first-child) {
            padding-left: 8px; } }

    &__link {
        color: #777777;
        display: flex;
        text-decoration: none;
        transition: opacity .2s;
        white-space: nowrap;

        &:hover {
            opacity: .8; } }

    &__link-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        transform: rotate(-90deg);
        margin-left: 8px;

        svg {
            width: 20px;
            height: 20px;
            fill:  #777777; } } }

