.mobile-modal {
    width: 100%;
    height: 100%;
    position: fixed;
    content: '';
    top: 0;
    left: 0;
    background-color: #ffffff;
    z-index: 100;
    display: none;

    &--active {
        display: block; }

    &__inner {
        width: 100%;
        height: 100%;
        overflow-y: auto; }

    &__close {
        margin-right: -10px; }

    &__wrapper {

        &--hidden {
            display: none; } }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding-left: 16px;
        padding-right: 16px;
        padding-top: 18px;
        padding-bottom: 18px;
        box-sizing: border-box; }


    &__title {
        font-size: 20px;
        line-height: 24px;
        color: $main-dark;
        font-family: $bold; }

    &__search {
        padding-left: 16px;
        padding-right: 16px;
        width: 100%;
        box-sizing: border-box;
        margin-bottom: 28px;
        margin-top: 16px; }

    &__nav-list {
        margin: 0;
        padding: 0;
        list-style: none; }

    &__nav-item {
        margin-bottom: 16px; }

    &__nav-link {
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-decoration: none;
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 16px;
        padding-right: 16px;

        &:hover {
            background-color: $accent;

            .mobile-modal__nav-link-title {
                color: #ffffff; }

            .mobile-modal__nav-link-icon {
                background-color: #ffffff;

                svg {
                    fill: $accent; } }

            .mobile-modal__nav-link-arrow {

                svg {
                    fill: #ffffff; } } } }

    &__nav-link-title {
        display: flex;
        align-items: center;
        font-size: 16px;
        color: $main-dark;
        line-height: 24px;
        font-family: $bold; }


    &__nav-link-icon {
        margin-right: 12px;
        width: 44px;
        height: 44px;
        border-radius:  4px;
        background-color: $accent;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            width: 24px;
            height: 24px;
            fill: #ffffff; } }

    &__nav-link-arrow {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: rotate(-90deg); }


    &__category-list {
        margin: 0;
        padding: 0;
        list-style: none; }

    &__category-item {
        border-bottom: 1px solid #ECF2F1; }

    &__category-link {
        font-size: 16px;
        line-height: 24px;
        color: $main-dark;
        display: block;
        padding-top: 16px;
        padding-bottom: 16px;
        padding-left: 16px;
        padding-right: 16px;
        font-family: $bold;
        text-decoration: none;
        transition: background .2s, color .2s;

        &:hover {
            background-color: $accent;
            color: #ffffff; }

        &--current {
            color: $accent; } }

    &__ad {
        padding: 16px;
        width: 100%;
        box-sizing: border-box; } }


