.header {
    background-color: #ffffff;
    border-bottom: 1px solid #ECF2F1;

    &__main {
        padding-top: 20px;
        padding-bottom: 20px; }

    &__container {
        max-width: 1280px;
        margin-left: auto;
        margin-right: auto;
        padding-left: 16px;
        padding-right: 16px; }


    &__main-nav {
        display: flex;
        align-items: center; }


    // Логотип в header

    &__logo {
        display: flex;
        align-items: center;
        text-decoration: none;

        svg {
            fill: $main-dark; } }

    &__logo-description {
        color: $main-dark;
        line-height: 24px;
        font-family: $bold;
        margin-left: 10px; }


    // Главное меню

    &__main-nav-list {
        display: none;

        @include md-block() {
            display: flex;
            margin: 0;
            padding: 0;
            list-style: none;
            margin-left: -8px;
            margin-right: -8px;
            margin-left: 124px; } }

    &__main-nav-item {
        margin-left: 8px;
        margin-right: 8px; }

    &__main-nav-link {
        display: inline-block;
        font-size: 16px;
        line-height: 40px;
        font-family: $bold;
        color: #929298;
        padding-left: 16px;
        padding-right: 16px;
        text-decoration: none;
        transition: color .2s;

        &:hover {
            color: $accent; } }


    // Поле поиска

    &__search-field {
        display: none;

        @include md-block() {
            display: block;
            margin-left: auto; } }


    // Кнопка мобильного меню

    &__mobile-menu {
        margin-left: auto;
        margin-right: -10px;

        @include md-block() {
            display: none; } }


    // Мобильная навигация в категориях

    &__mobile-nav {
        @include md-block() {
            display: none; } }

    &__mobile-button-nav {
        padding: 16px;
        border: none;
        display: flex;
        justify-content: space-between;
        width: 100%;
        font-size: 14px;
        line-height: 24px;
        font-family: $bold;
        color: $main-dark;
        background-color: #ffffff;
        border-top: 1px solid #ECF2F1;
        border-top: 1px solid #ECF2F1; }

    &__mobile-button-icon {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            fill: $main-dark; } }




    // Прозрачный header

    &--transparent {
        @include md-block() {
            background-color: transparent;
            border-bottom: none;

            .header__main-nav-link {
                color: #ffffff;

                &:hover {
                    color: #D0F0EA; } }

            .input-search__button {
                background-color: $main-dark;

                &:hover {
                    background-color: #49524f; } }

            .logo {
                svg {
                    fill: #ffffff; } }

            .logo__description {
                color: #ffffff; } } } }

