.burger {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    width: 24px;
    height: 24px;
    padding: 10px 10px;
    background: 0 0;
    border: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    span {
        position: relative;
        display: inline-block;
        width: 18px;
        height: 2px;
        vertical-align: bottom;
        background: $main-dark;
        transition: background .3s;

        &::before,&::after {
            content: "";
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            background: $main-dark; }



        &::before {
            top: 6px; }

        &::after {
            top: -6px; } }

    &--close {
        span {
            background-color: transparent;

            &::before {
                transform: rotate(45deg);
                transform-origin: 50% 50%;
                top: 0; }

            &::after {
                top: 0;
                transform: rotate(-45deg);
                transform-origin: 50% 50%; } } } }
