.social {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-left: -8px;
    margin-right: -8px;
    display: flex;

    &__item {
        margin-left: 8px;
        margin-right: 8px; }

    &__link {
        display: flex;
        width: 32px;
        height: 32px;
        justify-content: center;
        align-items: center;
        position: relative;

        svg {
            width: 18px;
            height: 18px;
            fill: $main-dark;
            transition: fill .2s;
            z-index: 1;
            position: relative; }



        &--vk {

            &:hover {
                svg {
                    fill: #299CCE; } } }

        &--telegram {

            &:hover {
                svg {
                    fill: #0086E7; } } }

        &--facebook {

            &:hover {
                svg {
                    fill: #1877F2; } } }

        &--ok {

            &:hover {
                svg {
                    fill: #FF9800; } } }

        &--twitter {

            &:hover {
                svg {
                    fill: #2DAAE1; } } } } }

