.card-banner {
    border-radius: 4px;
    border: 1px solid #ECF2F1;
    position: relative;
    overflow: hidden;
    position: relative;
    overflow: hidden;
    height: 100%;
    min-height: 350px;
    display: flex;
    align-items: stretch;

    &:hover {

        .card-banner__image::before {
            background-color: rgba(0,0,0,0.5); } }


    &__content {
        padding: 16px;
        position: relative;
        z-index: 4;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: column;
        text-align: center;
        box-sizing: border-box;

        @include md-block() {
            padding: 32px; } }

    &__content-link {
        position: absolute;
        content: '';
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 5; }

    &__content-ticket {
        margin-bottom: 12px; }

    &__content-title {
        font-size: 20px;
        line-height: 32px;
        color: #ffffff;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 0;

        @include md-block() {
            font-size: 28px;
            line-height: 36px; } }

    &__content-meta {
        font-size: 14px;
        line-height: 18px;
        color: #F3F3F3;
        margin-top: 16px; }

    &__image {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;

        &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: rgba(0,0,0,0.3);
            transition: background .2s;
            z-index: 2; }


        img {
            display: block;
            object-fit: cover;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1; } } }


