.pagination {
    margin-left: -6px;
    margin-right: -6px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;

    @include md-block() {
        justify-content: center; }

    &__item {

        @include md-block() {
            margin-left: 6px;
            margin-right: 6px; } }

    &__link {
        display: flex;
        justify-content: center;
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 9px;
        padding-right: 9px;
        min-width: 34px;
        box-sizing: border-box;
        font-size: 14px;
        line-height: 18px;
        color: #777777;
        background-color: transparent;
        text-decoration: none;
        transition: background .2s, color .2s;
        text-align: center;
        font-family: $bold;

        @include md-block() {
            padding-top: 11px;
            padding-bottom: 11px;
            padding-left: 12px;
            padding-right: 12px;
            min-width: 40px; }

        &:hover {
            background-color: #EAFBF7;
            color: $accent; }

        &--active {
            background-color: #EAFBF7;
            color: $accent;

            &:hover {
                background-color: #D4F7EF; } }

        &--forward {
            @media screen and (max-width: 992px) {
                padding-left: 0;
                padding-right: 0; } } }

    &__separator {
        display: flex;
        justify-content: center;
        padding-top: 11px;
        padding-bottom: 11px;
        padding-left: 12px;
        padding-right: 12px;
        min-width: 40px;
        box-sizing: border-box;
        font-size: 14px;
        line-height: 18px;
        color: #777777;
        background-color: transparent;
        text-decoration: none;
        text-align: center;
        font-family: $bold; } }

