.feature-section {
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
    border: 1px solid #ECF2F1;

    @include md-block() {
        box-shadow: none;
        border: none; }

    &__container {
        @include md-block() {
            @include wrapper(); } }

    &__row {
        display: flex;
        justify-content: center;
        // width: 100%
 }        // box-sizing: border-box



    &__col {
        @include size(4);
        flex-grow: 1;
        position: relative;
        z-index: 2;

        &:not(:last-child) {
            border-right: 1px solid #ECF2F1; }

        @include md-block() {
            @include col();
            @include size(4);

            &:not(:last-child) {
                border-right: none; } } } }
