.feature {
    position: relative;
    padding: 16px;
    background-color: #ffffff;
    transition: transform .2s;

    &:hover {

        .feature__title {
            color: $accent; }

        @include md-block() {
            transform: translate3d(0,-10px, 0); } }

    @include md-block() {
        padding: 40px;
        min-height: 426px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08); }

    &__header {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        @include md-block() {
            flex-direction: row;
            justify-content: flex-start;
            margin-bottom: 24px; } }

    &__icon {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 4px;
        margin-bottom: 8px;

        @include md-block() {
            width: 64px;
            height: 64px;
            margin-right: 28px;
            margin-bottom: 0;
            background-color: $accent; }

        svg {
            width: 24px;
            height: 24px;
            fill: $accent;

            @include md-block() {
                width: 32px;
                height: 32px;
                fill: #ffffff; } } }

    &__title {
        font-size: 16px;
        line-height: 24px;
        color: $main-dark;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 0;
        transition: color .2s;

        @include md-block() {
            font-size: 24px;
            line-height: 28px;
            margin-top: 0;
            margin-bottom: 0; } }

    &__body {
        display: none;

        @include md-block() {
            display: block; } }

    &__text {
        font-size: 16px;
        line-height: 28px;
        color: $main-dark;
        margin: 0; }

    &__footer {
        display: none;

        @include md-block() {
            display: block;
            border-top: 1px solid #E0E7FB;
            padding-top: 24px;
            margin-top: auto; } }

    &__link {
        position: absolute;
        content: '';
        left: 0;
        top: 0;
        right: 0;
        bottom: 0; }


    &__more {
        font-size: 16px;
        line-height: 24px;
        color: $accent;
        font-family: $bold;
        text-decoration: none; } }

