.about-section {
    padding-top: 50px;
    padding-bottom: 90px;

    @include md-block() {
        padding-top: 180px;
        padding-bottom: 140px; }

    &__container {
        @include wrapper();
        padding-left: 16px;
        padding-right: 16px; }

    &__title {
        font-size: 24px;
        line-height: 40px;
        margin-top: 0;
        font-family: $bold;
        margin-bottom: 16px;

        @include md-block() {
            font-size: 40px;
            line-height: 52px;
            margin-bottom: 32px; } }

    &__description {
        font-size: 16px;
        line-height: 28px;
        margin-top: 0;
        margin-bottom: 0;
        max-width: 670px;
        color: #515154;

        @include md-block() {
            font-size: 18px;
            line-height: 30px; } } }
