html {
    margin: 0;
    padding: 0; }

.page {
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-family: $regular;
    background-color: #ffffff;

    &__container {
        max-width: 1280px;
        margin-left: auto;
        margin-right: auto;

        &--border {

            @include md-block() {
                border-left: 1px solid #ECF2F1;
                border-right: 1px solid #ECF2F1; } } }

    &__container-inner {
        display: flex; }

    &__sidebar {
        display: none;

        @include md-block() {
            display: block;
            max-width: 244px;
            box-sizing: border-box;
            border-right: 1px solid #ECF2F1;
            width: 100%;
            padding-left: 50px;
            padding-right: 50px;
            padding-top: 48px;
            padding-bottom: 48px; } }

    &__side-nav {
        margin-left: -16px;
        margin-right: -16px; }

    &__side-ad-banner {
        margin-left: -16px;
        margin-right: -16px;
        margin-top: 28px;
        position: sticky;
        top: 32px; }


    &__content {
        padding-left: 16px;
        padding-right: 16px;
        padding-bottom: 70px;
        width: 100%;
        box-sizing: border-box;

        @include md-block() {
            padding-left: 50px;
            padding-right: 50px;
            padding-top: 48px; } }


    &__content-row {


        @include md-block() {
            display: flex;
            flex-wrap: wrap;
            margin-left: -16px;
            margin-right: -16px; } }

    &__content-col {
        margin-bottom: 32px;
        @include md-block() {
            @include col(); } }

    &__content-col-4-md {
        @include size-md(4); }

    &__content-col-8-md {
        @include size-md(8); }

    &__content-header {
        padding-left: 16px;
        padding-right: 16px;
        padding-bottom: 22px;
        padding-top: 32px;

        @include md-block() {
            padding-left: 50px;
            padding-right: 50px;
            padding-top: 48px;
            padding-bottom: 48px;
            border-bottom: 1px solid #ECF2F1; } }

    &__title {
        font-size: 24px;
        margin: 0;
        line-height: 32px;
        font-family: $bold;

        @include md-block() {
            font-size: 32px;
            line-height: 40px; } }


    // Пагинация

    &__pagination {
        margin-top: 52px;

        @include md-block() {
            margin-top: 22px; } }

    &__pagination-more {
        display: flex;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;

        @include md-block() {
            max-width: 300px; } }

    &__pagination-nav {
        display: flex;
        justify-content: center;
        margin-top: 40px; }


    // Рекламный банер

    &__banner {
        width: 100%;
        height: 280px;
        background-color: #F5F5F5;

        &--content {
            margin-left: -16px;
            margin-right: -16px;
            width: calc(100% + 32px);

            @include md-block() {
                width: calc(100% + 100px);
                margin-left: -50px;
                margin-right: -50px; } }

        &--mb-32 {
            margin-bottom: 32px; } }


    // Первый экран главной страницы

    &__main-header {
        min-height: 350px;
        position: relative;

        @include md-block() {
            min-height: 750px;
            background-image: url('../img/main-header/main-header-bg.webp');
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat; }

        &::before {
            position: absolute;
            content: '';
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            background-color: rgba(29, 191, 159, 0.9); } }

    &__main-header-content {
        position: relative;
        z-index: 2; }

    &__main-header-title {
        font-size: 26px;
        line-height: 34px;
        margin-top: 52px;
        color:  #ffffff;
        font-family: $bold;
        text-align: center;
        max-width: 860px;
        margin-left: auto;
        margin-right: auto;
        padding-left: 16px;
        padding-right: 16px;
        padding-bottom: 50px;

        @include md-block() {
            font-size: 48px;
            line-height: 64px;
            margin-top: 72px; } }


    &__feature-section {
        margin-top: -50px;
        padding-left: 16px;
        padding-right: 16px;
        @include md-block() {
            margin-top: -280px; } }

    &__breadcrumbs {
        padding-left: 16px;
        padding-right: 0;
        overflow-x: auto;
        margin-bottom: 18px;

        @include md-block() {
            padding-left: 50px;
            padding-right: 50px;
            margin-bottom: 22px; } }


    &__article-layout {
        padding-bottom: 34px;
        padding-left: 16px;
        padding-right: 16px;

        @include md-block() {
            padding-left: 50px;
            padding-right: 50px;
            display: grid;
            grid-template-columns: calc(100% - 380px) 300px;
            grid-gap: 80px;
            padding-bottom: 50px; } }

    &__article-layout-main {}

    &__article-layout-aside {
        display: block;
        margin-top: 32px;

        @include md-block() {
            margin-top: 0; } }

    &__article-layout-ad {
        position: sticky;
        top: 32px; }

    &__content-layout {
        padding-left: 16px;
        padding-right: 16px;
        padding-bottom: 30px;

        @include md-block() {
            padding-left: 50px;
            padding-right: 50px;
            padding-bottom: 40px; } }

    &__content-layout-ad {
        position: sticky;
        top: 32px; }


    &__more {
        padding-left: 16px;
        padding-right: 16px;
        padding-top: 30px;
        padding-bottom: 30px;

        @include md-block() {
            padding-left: 50px;
            padding-right: 50px;
            padding-bottom: 48px; } }


    &__more-title {
        font-size: 20px;
        line-height: 30px;
        color: $main-dark;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 28px;

        @include md-block() {
            font-size: 28px;
            line-height: 36px; } }


    &__more-row {


        @include md-block() {
            display: flex;
            margin-left: -16px;
            margin-right: -16px;
            flex-wrap: wrap; } }

    &__more-col {
        margin-bottom: 32px;
        @include md-block() {
            @include col(); } }

    &__more-col-3-md {
        @include size-md(3); }

    &--unscrollable {
        overflow: hidden; } }



