.footer {
    border-top: 1px solid #ECF2F1;
    padding-top: 72px;
    padding-bottom: 72px;

    @include md-block() {
        padding-top: 54px;
        padding-bottom: 54px; }

    &__container {
        @include wrapper(); }

    &__row {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        align-items: center;

        @include md-block() {
            flex-direction: row; } }

    &__item {
        @include md-block() {
            width: 33.33%;
            display: flex; }

        &--logo {
            margin-bottom: 28px;
            @include md-block() {
                justify-content: flex-start;
                margin-bottom: 0; } }

        &--copyright {
            @include md-block() {
                justify-content: flex-end; } }

        &--social {
            margin-bottom: 52px;
            order: -1;

            @include md-block() {
                margin-bottom: 0;
                order: 0;
                justify-content: center; } } }

    &__logo {}

    &__copyright {
        font-size: 16px;
        line-height: 28px;
        color: #A9A9A9;
        font-family: $bold; } }

