.banner-offer {
    background-color: $accent;
    padding-top: 48px;
    padding-bottom: 48px;
    padding-right: 16px;
    padding-left: 16px;
    color: #ffffff;

    @include md-block() {
        border-radius: 6px;
        padding-top: 90px;
        padding-bottom: 90px;
        padding-right: 60px;
        padding-left: 60px; }


    &__logo {
        margin-left: auto;
        margin-right: auto;
        width: 42px;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 100%;
            height: auto; } }

    &__title {
        font-size: 24px;
        font-family: $bold;
        line-height: 40px;
        text-align: center;
        max-width: 635px;
        margin-left: auto;
        margin-right: auto;

        @include md-block() {
            font-size: 40px;
            line-height: 50px; } }

    &__description {
        font-size: 16px;
        line-height: 28px;
        max-width: 635px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;

        @include md-block() {
            font-size: 18px;
            line-height: 30px; } }

    &__social {
        display: flex;
        justify-content: center;
        margin-top: 32px;

        @include md-block() {
            margin-top: 60px; }

        .social svg {
            fill: #ffffff; } } }
