.banner {
    position: relative;
    display: flex;
    margin-top: 32px;

    @include md-block() {
        margin-top: 160px; }

    &__screenshot {
        display: none;

        @include md-block() {
            position: absolute;
            content: '';
            top: -40px;
            right: 52%;
            z-index: 1;
            overflow: hidden;
            border-radius: 6px;
            overflow: hidden;
            display: block;

            img {
                display: block; } }

        @media screen and (min-width: 1200px) {
            right: 47%; } }

    &__content {
        @include md-block() {
            margin-left: auto; } }

    &__content-list {
        margin: 0;
        padding: 0;
        list-style: none;
        max-width: 500px; }

    &__content-item {

        &:not(:last-child) {
            margin-bottom: 24px; }

        @include md-block() {
            &:not(:last-child) {
                margin-bottom: 60px; } } } }
