.book {

    &__title-wrapper {
        display: block;
        margin-bottom: 32px;

        @include md-block() {
            display: flex;
            flex-grow: 1;
            justify-content: space-between;
            align-items: center; } }

    &__title {
        font-size: 24px;
        line-height: 32px;
        color: $main-dark;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 0;

        @include md-block() {
            font-size: 32px;
            line-height: 40px; } }

    &__title-button {
        margin-top: 24px;

        @include md-block() {
            margin-top: 0; } }

    &__preview {
        margin-bottom: 30px;

        @include md-block() {
            margin-bottom: 40px; } }

    &__content-layout {
        padding-top: 30px;

        @include md-block() {
            display: grid;
            grid-template-columns: calc(100% - 380px) 300px;
            grid-gap: 80px;
            padding-top: 40px; } }

    &__content-layout-aside {
        padding-top: 30px;

        @include md-block() {
            padding-top: 0;
            display: block; } }

    &__content-layout-ad {
        @include md-block() {
            position: sticky;
            top: 32px; } }

    &__content-body {
        max-width: 640px;
        margin-left: auto;
        margin-right: auto; }

    &__title-description {
        font-size: 28px;
        line-height: 36px;
        color: $main-dark;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 24px; }


    &__text {
        font-size: 18px;
        line-height: 30px;
        font-family: $regular;
        color: #545454;
        margin-top: 0;
        margin-bottom: 0;

        &:not(:last-child) {
            margin-bottom: 24px; } } }
