.article {

    &__header {
        margin-bottom: 40px;

        @include md-block() {
            margin-bottom: 50px; } }

    &__ticket {
        margin-bottom: 16px; }

    &__title {
        font-size: 24px;
        line-height: 32px;
        color: $main-dark;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 0;

        @include md-block() {
            font-size: 32px;
            line-height: 42px;
            color: $main-dark;
            font-family: $bold; } }

    &__data {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px; }

    &__date {
        color: #777777;
        font-size: 14px;
        line-height: 20px; }

    &__social {
        position: fixed;
        z-index: 5;
        bottom: 0;
        left: 0;
        padding-top: 10px;
        padding-bottom: 10px;
        background-color: #ffffff;
        border-top: 1px solid #ECF2F1;
        width: 100%;
        padding-left: 24px;
        padding-right: 24px;
        box-sizing: border-box;

        .social {
            justify-content: space-between; }

        .social__link--vk {

            svg {
                fill: #299CCE; } }

        .social__link--telegram {

            svg {
                fill: #0086E7; } }

        .social__link--facebook {

            svg {
                fill: #1877F2; } }

        .social__link--ok {

            svg {
                fill: #FF9800; } }

        .social__link--twitter {

            svg {
                fill: #2DAAE1; } }

        @include md-block() {
            position: static;
            width: auto;
            padding: 0;
            border-top: none; } }

    &__main-img {
        width: 100%;
        margin-bottom: 36px;
        border-radius: 4px;
        overflow: hidden;

        @include md-block() {
            margin-bottom: 62px; }

        img {
            width: 100%;
            max-width: 100%;
            display: block; } }

    &__body {
        max-width: 640px;
        margin-left: auto;
        margin-right: auto;

        p {
            font-size: 16px;
            line-height: 24px;
            color: #545454;

            @include md-block() {
                font-size: 18px;
                line-height: 30px;
                margin-bottom: 24px;
                margin-top: 0; } }

        h2 {
            font-size: 20px;
            line-height: 30px;
            color: $main-dark;
            margin-bottom: 24px;
            font-family: $bold;

            @include md-block() {
                font-size: 28px;
                line-height: 36px; } }

        h3 {
            font-size: 22px;
            line-height: 30px;
            color: $main-dark;
            margin-bottom: 24px;
            font-family: $bold; }

        h4 {
            font-size: 20px;
            line-height: 28px;
            color: $main-dark;
            margin-bottom: 24px;
            font-family: $bold; }

        h5, h6 {
            ine-height: 18px;
            color: $main-dark;
            margin-bottom: 24px;
            font-family: $bold; }

        p + h1,  p + h2,  p + h3,  p + h4,  p + h5,  p + h6, p + ul, ul + p, ol + p, p + ol {
            margin-top: 40px; }

        figure {
            margin: 0;
            margin-top: 40px;
            margin-bottom: 40px;
            border-radius: 4px;
            overflow: hidden;

            img {
                max-width: 100%;
                display: block; }

            figcaption {
                font-size: 14px;
                color: #777777;
                line-height: 20px;
                font-family: $regular;
                margin-top: 10px; } }

        figure + p {
            margin-top: 40px; }

        ul {
            font-family: $regular;
            font-size: 16px;
            line-height: 24px;
            color: #545454;
            list-style: none;
            padding-left: 0;

            @include md-block() {
                font-size: 18px;
                line-height: 30px; }

            li {
                position: relative;
                padding-left: 20px;

                @include md-block() {
                    padding-left: 28px; }

                &:not(:first-child) {
                    margin-top: 12px; }

                &::before {
                    position: absolute;
                    content: '';
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background-color: #545454;
                    top: 12px;
                    left: 0;

                    @include md-block() {
                        left: 8px; } } } }

        ol {
            font-family: $regular;
            font-size: 16px;
            line-height: 24px;
            color: #545454;
            padding-left: 20px;

            @include md-block() {
                padding-left: 28px;
                font-size: 18px;
                line-height: 30px; }

            li {
                position: relative;
                padding-left: 4px;

                &::marker {
                    font-family: $bold;
                    font-size: 16px; }

                &:not(:first-child) {
                    margin-top: 12px; } } }


        a {
            color: $accent;

            &:hover {
                text-decoration: none; } } }


    &__banner {
        margin-top: 40px;
        margin-left: -16px;
        margin-right: -16px;

        .banner-offer {
            padding-top: 48px;
            padding-bottom: 48px;

            @include md-block() {
                padding-top: 64px;
                padding-bottom: 64px; } } }

    code {
        border-radius: 4px;
        margin-top: 24px;
        margin-bottom: 24px; }


    pre + h1,h3,h4,h5,h6 {
            margin-top: 40px; } }

