.player {
    background-color: #C4C4C4;
    overflow: hidden;
    position: relative;

    @include md-block() {
        padding-right: 230px; }

    &__main {
        position: relative;
        padding-bottom: 56.25%;
        height: 0;

        iframe {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%; } }

    &__aside {
        background-color: #7E7E7E;


        @include md-block() {
            display: flex;
            flex-direction: column;
            max-height: 100%;
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            overflow-y: auto;
            width: 230px; } }

    &__aside-title {
        padding-left: 24px;
        padding-right: 24px;
        padding-top: 22px;
        color: #d6d6d6;
        font-size: 16px;
        line-height: 20px;
        font-family: $bold;
        margin-bottom: 6px;
        background-color: #7E7E7E;


        @include md-block() {
            position: sticky;
            top: 0;
            left: 0;
            content: '';
            right: 0; } }

    &__list {
        display: none;

        @include md-block() {
            margin: 0;
            padding: 0;
            list-style: none;
            flex-grow: 1;
            display: block; } }

    &__list-mobile {
        position: relative;

        @include md-block() {
            display: none; } }

    &__list-mobile-select {
        position: absolute;
        content: '';
        opacity: 0;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        border: none;
        z-index: 2; }

    &__snippet {
        width: 100%;
        padding: 12px 24px;
        border: none;
        background-color: transparent;
        display: flex;
        flex-direction: column;
        color: #ffffff;
        font-family: $regular;
        transition: background .2s;
        cursor: pointer;
        box-sizing: border-box;

        &:hover {
            background-color: #a0a0a0; }

        &--current {
            background-color: #434343;

            &:hover {
                background-color: #434343; } } }

    &__snippet-header {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 12px; }

    &__snippet-number {
        font-size: 12px;
        line-height: 16px;
        letter-spacing: .06em; }

    &__snippet-duration {
        font-size: 12px;
        line-height: 16px;
        letter-spacing: .06em; }

    &__snippet-title {
        font-size: 14px;
        line-height: 21px;
        font-family: $bold;
        letter-spacing: 0.02em; } }


