.card {
    border-radius: 4px;
    border: 1px solid #ECF2F1;
    position: relative;
    overflow: hidden;
    transition: transform .2s;

    &:hover {
        transform: translate3d(0, -10px, 0);

        .card__image {
            opacity: .7; } }


    &__image {
        transition: opacity .2s;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 0;
        padding-bottom: 56.525%;

        img {
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            display: block;
            object-fit: cover; } }

    &__link {
        position: absolute;
        content: '';
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 2; }

    &__content {
        padding: 20px; }

    &__ticket {
        margin-bottom: 8px; }

    &__title {
        font-size: 18px;
        line-height: 24px;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 0; }

    &__footer {
        margin-top: 24px;
        display: flex; }

    &__footer-item {
        color: #515154;
        font-size: 14px;
        line-height: 18px;


        &--duration {
            margin-left: 24px; } }


    // Карточка видео урока

    &--video {

        .card__title {

            @include md-block() {
                height: 48px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden; } } }

    // Карточка статьи

    &--article {

        .card__title {

            @include md-block() {
                height: 96px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
                overflow: hidden; } } }


    // Карточка книги

    &--book {

        .card__title {

            @include md-block() {
                height: 96px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
                overflow: hidden; } }

        .card__image {
            width: 112px;
            height: 137px;
            margin-left: auto;
            margin-right: auto;
            padding-top: 12px;
            padding-bottom: 12px;
            position: relative;

            img {
                position: static; } } } }
